@use '../util' as *;
@use '../globals' as *;

/*-----------------------------
*GLOBAL NAVBAR STARTS
 ------------------------------ */
.header{
    width: 100%;
    max-height: 70px;
}

.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: rem(16) rem(24);
    padding: 0;
    width: 100%;
    max-height: 70px;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: var(--white);
    box-shadow: 0px 0px 10px gray;
}

.navbar__logo{
    position: relative;
    left: rem(55);
}

.logo{
    width:rem(190);
    height:100%;
}

.menu__list{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu__item{
    margin: 0 rem(35);
}

.item__link{
    font-size: rem(18);
    font-weight: 700;
    color: rgb(0,0,0);
    padding-bottom: 10px;
    position: relative;
}

/* ====> hover effect -> underlined links */
.item__link.underlined:hover{
    color: var(--violet-hocarre);
}

.item__link.underlined::before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: rem(2);
    background-color: var(--violet-hocarre);
    transform: scaleX(0);
    transition: transform 0.4s ease;
}

.item__link.underlined:hover::before{
    transform: scaleX(1);
}

/* ====> signUp CTA button*/

.menu__item.signUp{
    background-image: linear-gradient(to right, #9D50BB 10%, var(--violet-hocarre) 50%, #9D50BB  100%);
    background-size: 200% auto;
    border-radius: 10px;
    padding: 5px 25px;
    transition: .5s;
}

.menu__item.signUp:hover{
    background-position: right center;
}

.menu__item.signUp .item__link{
    color: var(--white);
}


         .btn-grad {background-image: linear-gradient(to right, #6a3093 0%, #a044ff  51%, #6a3093  100%)}
         .btn-grad {
            margin: 10px;
            padding: 15px 45px;
            text-align: center;
            text-transform: uppercase;
            transition: 0.5s;
            background-size: 200% auto;
            color: white;            
            box-shadow: 0 0 20px #eee;
            border-radius: 10px;
            display: block;
          }

          .btn-grad:hover {
            background-position: right center; /* change the direction of the change here */
            color: #fff;
            text-decoration: none;
          }
         

/* ------------------------------------
*GLOBAL NAVBAR ENDS
--------------------------------  */


/*--------------------------------
*HAMBURGER MENU STARTS
--------------------------------- */
.menu__hamburger{
    border: none;
    background-color: var(--white);
}

.menu__hamburger{
    display: none;
}

.menu__bar{
    display: block;
    width: rem(25);
    height: rem(3);
    margin: rem(5) auto;
    transition: all 0.3s ease-in-out;
    background-color: var(--violet-hocarre);
}

/*----------------------------------
*HAMBURGER MENU ENDS
-----------------------------------  */



/*----------------------------------
*MOBILE VERSION STARTS
-----------------------------------  */

@media only screen and (max-width: rem(865)){

    .navbar{
        justify-content: space-between;
    }

    .menu__list{
        position: fixed;
        left: -1000%;
        top: rem(55);
        flex-direction: column;
        width: 100%;
        border-radius: rem(10);
        text-align: center;
        transition: .7s;
        background-color: var(--white);
    }

    .menu__list.menu--is-active{
        left: 0;
    }

    .menu__item{
        margin: rem(25) 0;
    }

    .menu__hamburger{
        display: block;
        cursor: pointer;
        margin-right: rem(30);
        justify-content: flex-end;
    }

    .menu__hamburger.menu--is-active .menu__bar:nth-child(2){
        opacity: 0;
    }

    .menu__hamburger.menu--is-active .menu__bar:nth-child(1){
        transform: translateY(8px) rotate(45deg);
    }

    .menu__hamburger.menu--is-active .menu__bar:nth-child(3){
        transform: translateY(-8px) rotate(-45deg);
    }
}

/*----------------------------------
*HAMBURGER VERSION ENDS
-----------------------------------  */
